.rakuie-ceo {
  padding: 80px;
  background: #efefef;
}

.rakuie-ceo .inner {
  width: 1120px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
}

.rakuie-ceo .inner img {
  display: block;
  width: 543px;
  flex: 1 0 auto;
}

.rakuie-ceo .inner p {
  margin-right: 40px;
  background: #fff;
  padding: 30px;
  font-size: 1.8rem;
  line-height: 1.5em;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
  position: relative;
}

.rakuie-ceo .inner p::after {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 15px 0 20px 10px;
  border-color: transparent transparent transparent #fff;
  position: absolute;
  top: 50%;
  left: 100%;
  transform: translateY(-50%);
}
